import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';
import { engine } from '../engine';
import { SourceType } from '../types';
import { merge } from 'rxjs';
import { map } from 'rxjs/operators';

const Container = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  bottom: 20vh;
  font-size: 5vh;
  text-shadow: 2px 2px 2px #111;
  // -webkit-text-stroke-width: 2px;
  // -webkit-text-stroke-color: #111;

  .text-1 {
    color: #dcf;
  }
  .text-2 {
    color: #dfc;
  }
`;
const Text = styled.div``;
let timers: NodeJS.Timeout[] = [];
type Props = {
  enabled: boolean;
};
const Scope: React.FC<Props> = ({ enabled }) => {
  const [text1, setText1] = useState('');
  const [text2, setText2] = useState('');
  const show = useCallback(
    (text: string, force: boolean = false) => {
      const index = text.startsWith('2 - ') ? 1 : 0;
      const setText = [setText1, setText2][index];

      if (!enabled && !force) {
        return;
      }
      setText(text);
      if (timers[index]) {
        clearTimeout(timers[index]);
      }
      timers[index] = setTimeout(() => {
        setText('');
      }, 1000);
    },
    [setText1, setText2, enabled]
  );
  useEffect(() => {
    const sub = merge(
      engine.scopeSubjects.sourceTypeChange.pipe(map((t) => SourceType[t].toString())),
      engine.scopeSubjects.parameterChange.pipe(
        map((e) => `${e.sourceIndex + 1} - ${e.parameter}: ${e.value.toFixed(2)}`)
      ),
      engine.scopeSubjects.lfoChange.pipe(
        map((e) => `${e.sourceIndex + 1} - LFO - ${e.parameter}: ${Math.floor(e.value*100)}%`)
      ),
      engine.scopeSubjects.loadPreset.pipe(map((i) => `Load preset ${i + 1}`)),
      engine.scopeSubjects.savePreset.pipe(map((i) => `Save preset ${i + 1}`))
    ).subscribe(show);
    return () => sub.unsubscribe();
  }, [show]);
  useEffect(() => {
    show(`Scope ${enabled ? 'on' : 'off'}`, true);
  }, [enabled, show]);
  return (
    <Container>
      <Text className="text-1">{text1}</Text>
      <Text className="text-2">{text2}</Text>
    </Container>
  );
};
export default Scope;
